.container {
    /* border: 1px solid rgb(11, 128, 128); */
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 720px;
    height: 280px;
    margin: 0 auto;
    background-image: url('./media-images/bg-1.jpg');
    background-size: cover;
}
/* .bgimage {
    display: block;
    border-radius: 5px;
} */
.itemsContainer {
    display: flex;
    flex-direction: column;
    position: relative;
    max-width: 180px;
    max-height: 180px;
    margin-left: 30px;
    border-radius: 50%;
    box-shadow: 2px 2px 10px 5px rgb(8, 92, 92);
}
.imgRounded {
    border-radius: 50%;
    max-width: 180px;
    max-height: 180px;
}
.ulHorizontal {
    /* margin-left: 5px; */
    display: flex;
    flex-direction: row;
    list-style: none;
    align-items: center;
    max-width: 200px;
}
.ulHorizontal li {
    float: inherit;
    padding-right: 18px;
}
.icon {
    max-height: 25px;
    box-shadow: 1px 1px 5px 2px rgb(8, 92, 92);
}
.icon:hover {
    transform: scale(1.1);
    cursor: pointer;
}

@media (max-width:720px){
    .container {
      width: 100%;
    }
}