.container {
    /* border: 1px solid rgb(11, 128, 128); */
    background-color: rgb(14, 189, 189);
    background-image: linear-gradient( rgb(25, 173, 173),#0b8080);
    /* #0b8080 */
    display: flex;
    flex-direction: column;
    width: 720px;
    height: auto;
    margin: 0 auto;
}
.alltext {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
    font-family: Roboto-light;
}
.titles {
    text-align: center;
}
.subTitles {
    font-size: 18px;
    text-shadow: 1px 1px rgb(12, 54, 54);
    color: whitesmoke;
}
.colorTitles {
    text-shadow: 1px 2px rgb(12, 54, 54);
    color: rgb(250, 246, 246);
}
.textItalic {
    font-style: italic;
}
.readingText {
    padding: 30px;
    text-align: justify;
    color: rgb(255, 255, 255);
}
.containerImage {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 70%;
    height: 312px;
    margin: 0 auto;
    border-radius: 5px;
    box-shadow: 2px 3px 8px 1px rgb(49, 49, 49);
}
.imgBig {
    border-radius: 5px;
    display: block;
    width: 100%;
    height: 100%;
    overflow: hidden;
}
.miniText {
    font-size: 0.7em;
    padding-bottom: 20px;
}
.iframeContainer {
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 85%;
    margin: 0 auto;
}
@media (max-width:720px){
    .container {
      width: 100%;
    }
    .containerImage {
        height: 200px;
    }
}