.footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    justify-items: center;
    align-items: center;
    width: 720px;
    height: 120px;
    margin: 0 auto;
}
.textFooter {
    color: rgb(16, 53, 16);
    font-size: 0.9em;
    line-height: 5px;
    font-family: Roboto-light;
}
@media (max-width:720px){
    .footer {
      width: 100%;
    }
}