.container {
    display: flex;
    flex-direction: column;
    justify-items: center;
    justify-content: center;
    align-items: center;
    width: 720px;
    height: 450px;
    background-color: #0b8080;
    margin: 0 auto;
}
.form {
    display: flex;
    flex-direction: column;
    width: 60%;
    margin: 0 auto;
}
.colorText {
    color: whitesmoke;
    font-family: Roboto-light;
}
.textTitle {
    text-shadow: 1px 1px black;
    font-family: Roboto-light;
}


@media (max-width:720px){
    .container {
      width: 100%;
    }
}